import React from "react";
import { Button, Modal, Col, Form } from "antd";
import { LoadingOutlined } from "@ant-design/icons";

const CModal = ({
  id = 0,
  title = String,
  onSubmit = () => {},
  onCancel = () => {},
  open = Boolean,
  isSubmit = false,
  children = <div>Hello World!</div>,
}) => {
  const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 4 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 20 },
    },
  };

  return (
    <Modal
      closable={false}
      title={<Col style={{ textAlign: "center" }}>{title}</Col>}
      open={open}
      footer={
        id > 0
          ? [
              <Button
                key="cancel"
                style={{ backgroundColor: "#FFC107", color: "white" }}
                onClick={onCancel}
              >
                Cancel
              </Button>,
              <Button
                key="update"
                style={{ backgroundColor: "#198754", color: "white" }}
                onClick={onSubmit}
                icon={isSubmit && <LoadingOutlined />}
                disabled={isSubmit ? true : false}
              >
                Update
              </Button>,
            ]
          : [
              <Button
                key="back"
                style={{ backgroundColor: "#FFC107", color: "white" }}
                onClick={onCancel}
              >
                Cancel
              </Button>,
              <Button
                key="insert"
                type="primary"
                icon={isSubmit && <LoadingOutlined />}
                disabled={isSubmit ? true : false}
                onClick={onSubmit}
              >
                Submit
              </Button>,
            ]
      }
    >
      <Form {...formItemLayout}>{children}</Form>
    </Modal>
  );
};

export default CModal;
